"use strict";

const _ = require("lodash");

module.exports = {
    appendChild(node, child) {
        if (!node.children) node.children = [];
        node.children.push(child);
    },

    insertBefore(node, child, before) {
        if (!node.children) node.children = [];
        const index = node.children.indexOf(before);
        node.children.splice(index, 0, child);
    },

    isEmpty(node) {
        return _.isEmpty(node.children) && !_.isEmpty(node.attributes);
    },

    removeChild(node, predicate) {
        _.remove(node.children, predicate);
    },

    hasChild(node, predicate) {
        return _.some(node.children, predicate);
    },

    findChild(node, childPredicate) {
        return _.find(node.children, childPredicate);
    },

    getChildAttribute(node, childPredicate, attribute) {
        const child = this.findChild(node, childPredicate);
        if (child) return child.attributes && child.attributes[attribute];
    },

    setChildAttributes(node, childPredicate, sets) {
        let child = this.findChild(node, childPredicate);
        _.forOwn((value, name) => {
            console.log(name, value)
            if (_.isNil(value)) {
                if (child) delete child[name];
            } else {
                if (!child) {
                    child = _.cloneDeep(childPredicate);
                    this.appendChild(node, child);
                }

                child[name] = value;
            }
        });
    },

    removeChildIfEmpty(node, childPredicate) {
        const child = this.findChild(node, childPredicate);
        if (child && this.isEmpty(child)) this.removeChild(node, childPredicate);
    }
};
